@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/OpenSans-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/OpenSans-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.section-title {
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}

.big-button {
  display: inline-block;
  width: 100%;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  color: #444444;
  background-color: #ffffff;
  padding-top: 14px;
  padding-bottom: 14px;
  box-shadow: inset 0 0 0 2px #444444;
  transition: 0.1s ease-in;
}

.big-button:hover {
  color: #ffffff;
  background-color: #444444;
}

.big-button:active {
  color: rgba(255, 255, 255, 0.3);
}

.page-body {
  min-width: 320px;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  color: #444444;
  background-color: #ffffff;
  margin: 0;
}

.main-header {
  position: relative;
  min-width: 320px;
  background-color: #fafafa;
  display: flex;
  flex-direction: column;
}
.main-header::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 89px;
  background-color: #ffffff;
}
.main-header__logo {
  align-self: center;
  display: inline-block;
  padding-top: 22px;
  padding-bottom: 18px;
  margin-right: 2px;
  z-index: 3;
}
.main-header__toggle {
  display: none;
}
.main-header__nav-list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  text-align: left;
  padding: 0;
  margin: 0 auto;
  display: none;
}
.main-header__nav-item {
  display: block;
  box-shadow: 0px 1px 0px #ececec, inset 0px 1px 0px #ececec;
}
.main-header__nav-item-link {
  display: inline-block;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  color: #000000;
  text-decoration: none;
  padding-top: 30px;
  padding-bottom: 26px;
  margin-left: 76px;
  transition: 0.2s ease-out;
}
.main-header__nav-item--new, .main-header__nav-item--sale {
  display: none;
}
.main-header__bottom-line {
  display: none;
}
.main-header__free-delievery {
  display: none;
}

.search {
  margin-left: 32px;
}

.bin {
  margin-left: 30px;
}

.nav-icon {
  fill: rgba(0, 0, 0, 0.2);
}

.icon-search {
  margin-right: 22px;
}

.icon-bin {
  margin-right: 21px;
}

.catalog-header {
  background-color: #f1f1f1;
}
.catalog-header::before {
  height: 79px;
}

.form-header {
  background-color: #f1f1f1;
}
.form-header::before {
  height: 79px;
}

@media (min-width: 768px) {
  .main-header {
    width: 100%;
  }
  .main-header__logo {
    align-self: start;
    padding-left: 48px;
    margin-right: auto;
  }
  .main-header__logo {
    margin-left: 2px;
  }
  .main-header__bottom-line {
    display: block;
    margin: 0;
    margin-top: -2px;
    border: 0.5px solid #ececec;
  }
  .main-header__nav-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 350px;
    margin: 0 auto 0 50px;
  }
  .main-header__nav-item {
    box-shadow: none;
  }
  .main-header__nav-item--search {
    position: absolute;
    top: 0;
    right: 315px;
    box-shadow: none;
    border-left: 1px solid #ececec;
  }
  .main-header__nav-item--bin {
    position: absolute;
    top: 0;
    right: 0;
    box-shadow: none;
    border-left: 1px solid #ececec;
  }
  .main-header__nav-item-link {
    padding-top: 22px;
    padding-bottom: 33px;
    margin-left: 0;
  }
  .main-header .search {
    padding: 36px 31px 31px 33px;
  }
  .main-header .icon-search {
    margin-right: 0;
  }
  .main-header__search-text {
    display: none;
  }
  .main-header .bin {
    padding-top: 34px;
    padding-right: 50px;
    padding-left: 31px;
  }
  .main-header .icon-bin {
    margin-right: 26px;
  }
  .main-header .to-catalog, .main-header .knitting {
    font-size: 15px;
    margin-right: 37px;
    padding-bottom: 22px;
  }
  .main-header__free-delievery {
    position: absolute;
    top: 96px;
    right: 50px;
    display: block;
  }
  .catalog-header::before {
    height: 88px;
  }
  .form-header::before {
    height: 88px;
  }
  .form-header__logo {
    margin-left: 0;
  }
}
@media (min-width: 1150px) {
  .main-header {
    width: 1150px;
    margin: 0 auto;
  }
  .main-header__logo {
    align-self: center;
    margin: 0;
    margin-right: 1px;
    padding: 9px 0 5px;
    transition: 0.11s ease-out;
  }
  .main-header__logo:hover {
    opacity: 0.6;
  }
  .main-header__logo:active {
    opacity: 0.3;
  }
  .main-header__nav-item-link {
    transition: 0.11s ease-out;
  }
  .main-header__nav-item:not(:nth-of-type(4)):hover {
    opacity: 0.6;
  }
  .main-header__nav-item:nth-of-type(3):hover .nav-icon {
    fill: rgb(99, 209, 187);
  }
  .main-header__nav-item:nth-of-type(4):hover .nav-icon {
    fill: rgb(99, 209, 187);
  }
  .main-header__nav-item:nth-of-type(4):hover .search-text {
    opacity: 0.6;
  }
  .main-header__nav-item:not(:nth-of-type(4)):active {
    opacity: 0.3;
  }
  .main-header__nav-item:nth-of-type(3):active .nav-icon {
    fill: rgba(99, 209, 187, 0.3);
  }
  .main-header__nav-item:nth-of-type(4):active .nav-icon {
    fill: rgba(99, 209, 187, 0.3);
  }
  .main-header__nav-item:nth-of-type(4):active .search-text {
    opacity: 0.3;
  }
  .main-header .search {
    padding: 36px 34px 31px 32px;
  }
  .main-header .bin {
    padding-right: 52px;
  }
  .main-header__nav-item--to-catalog {
    position: absolute;
    top: 0;
    left: 0;
    border-right: 1px solid #ececec;
  }
  .main-header .to-catalog {
    font-size: 17px;
    margin: 0;
    padding: 33px 37px 33px 50px;
  }
  .main-header__nav-item--knitting {
    position: absolute;
    top: 0;
    left: 235px;
  }
  .main-header .knitting {
    font-size: 17px;
    margin: 0;
    padding: 33px 37px 33px 35px;
  }
  .main-header__nav-item--new {
    display: block;
  }
  .main-header .new {
    font-size: 15px;
    padding-top: 20px;
    margin-left: 1px;
    margin-right: 62px;
  }
  .main-header__nav-item--sale {
    display: block;
  }
  .main-header .sale {
    font-size: 15px;
    padding-top: 20px;
  }
}
