.week-product {
  position: relative;
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
  background-color: #ffffff;
}
.week-product::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: -6px;
  width: 100%;
  height: 6px;
  background-image: url("../img/mobile-wave.svg");
  background-size: contain;
  background-repeat: repeat-x;
}
